@import "../../../Styles/variables.scss";

.link-style {
  display: flex;
  justify-content: left;
  gap: 10px;
  align-items: center;
  padding: $pad-1;
  text-decoration: none;
  transition: $trans-gen;
  i,
  p {
    text-decoration: none;
    //font-weight: bold;
    color: $dark-grey;
  }
}

.link-style.notification{
  .notifcation{
    span{
      padding-left: 0.4rem;
      padding-right: 0.4rem;
      background-color: $col-high;
      border-radius: 1rem;
      color: whitesmoke;
    }
  }
}


.link-style:hover {
  background-color: $light-grey;
}

.link-style.active {
  background-color: $dark-grey;
  border-right-color: $dark-grey;
  i,
  p {
    color: whitesmoke;
  }
}
